import React from 'react'
import {useCompositionInput} from "valor-hooks";

export const SearchInputDemo = () => {
  const compositionInputProps = useCompositionInput({defaultValue: '', onCommit: (v) => console.log('searching...', v)});

  return (<div>
      <div>, , </div>
      <div>, search</div>
      <input {...compositionInputProps}/>
    </div>
  )
}



